.about-us__feature-title-1 {
  background: url("../img/about-us-1.svg") center top/146px no-repeat;
}

.about-us__feature-title-2 {
  background: url("../img/about-us-2.svg") center top/206px no-repeat;
}

.about-us__feature-title-3 {
