.works {
  background: #ebebeb;
  padding-top: 65px;
  padding-bottom: 65px;
}

.works__title {
  margin-bottom: 73px;
}

.works__cols-container {
  display: flex;
  justify-content: space-between;
}

.works__item {
  position: relative;
  overflow: hidden;
  width: 45rem;
  height: 45rem;
  background: gray;
  transition: background 0.3s ease-in-out;
}

.works__item-overlay {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  padding: 8.2rem 5.2rem;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
  color: #fff;
  background: linear-gradient(rgba(98, 67, 111, 0.9), 65%, rgba(79, 84, 154, 0.9));
}

.works__item-overlay-title {
  font: 700 3rem "Ubuntu";
}

.works__item-overlay-subtitle {
  font: 300 3rem "Ubuntu";
}

.works__item:hover .works__item-overlay {
  opacity: 1;
}

.works__item-1,
.works__item-2,
.works__item-4,
.works__item-6 {
  margin-bottom: 5.6rem;
}

.works__item-5,
.works__item-6 {
